.Header
  color: white
  padding: 0
  margin: 0
  width: 100vw
  height: 18vh
  display: flex
  flex-direction: row
  justify-content: center
  align-items: center
  background: no-repeat linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url("./img/cover.jpg") center/cover

.HeaderSearch
  margin-bottom: 10px
  display: flex
  justify-content: center

.HeaderForm input
  height: 25px
  width: 40vw
  border-radius: 10px 0 0 10px
  border: none
  padding: 5px

.HeaderForm button
  width: fit-content
  height: 35px
  border-radius: 0 10px 10px 0
  border: 0
  color: white
  background: darkgray

  &:hover
    background-color: gray
    transition: 0.5s


.HeaderSelectGroup
  width: 100%
  margin-top: 20px
  display: flex
  text-align: center

.HeaderSelect select
  margin: 0 10px
  width: 87px
  height: 30px



