.Card
  width: 255px
  height: 420px
  box-shadow: 1px 1px 1px lightgray
  background: rgba(230, 223, 223, 0.18)
  display: flex
  flex-direction: column
  justify-content: space-between
  align-content: space-around
  margin: 5px
  padding: 10px
  &:hover
    box-shadow: 5px 5px 5px lightgray
    transition: all .3s

.CardCover
  width: 128px
  height: 207px
  align-self: center

.CardBody
  display: flex
  flex-direction: column

.CardTitle
  margin: 0
  padding: 0

.CardAuthor
  margin-top: 10px
  padding: 0

.CardYear
  display: flex
  margin: 0
  align-self: end
  justify-self: end
